import * as yup from "yup";

export const schema = yup.object().shape({
  email: yup.string().email(" email").required("Email "),
  password: yup
    .string()
    .required("   ")
    .min(6, "    6 ")
    .matches(/[a-z]/, "       ")
    .matches(/[A-Z]/, "       ")
    .matches(/[0-9]/, "      ")
    .matches(
      /[\W_]/,
      "       "
    ),
});
